div.tfw-view-input {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: stretch;
            align-items: stretch;
    margin: .5rem;
    transition: all .2s;
}

div.tfw-view-input.wide {
    display: -webkit-flex;
    display: flex;
}

div.tfw-view-input label {
    font-size: 80%;
    padding: 2px .25rem;
}

div.tfw-view-input div.error {
    font-size: 80%;
    font-weight: bold;
    padding: 2px .25rem;
}

div.tfw-view-input input {
    width: 100%;
    font-size: inherit;
    font-family: inherit;
    border: none;
    padding: 0 .25rem;
    line-height: 2rem;
    transition: all .2s;
}

label.tfw-view-Label {
    width: 100%;
    line-height: 1.2rem;
    font-size: 80%;
    font-weight: normal;
    padding: 0 .5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    background: transparent;
    text-align: left;
}

div.Install div.flex {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}

div.Install div.flex > div {
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
}

div.Install fieldset {
    margin: 3rem .5rem .5rem .5rem;
    border: 1px solid var(--thm-black-5);
    background: var(--thm-bg2);
}

div.Install fieldset > legend {
    margin-top: -2rem;
    line-height: 2rem;
    border: 1px solid var(--thm-black-5);
    border-bottom: none;
    background: var(--thm-bg2);
    border-radius: .5rem .5rem 0 0;
    padding: 0 1rem;
}

